  josefinSlab:              ( val: 'Josefin Slab',              path: 'Josefin+Slab:100,300,400,600,700'),
  kaushanScript:            ( val: 'Kaushan Script',            path: 'Kaushan+Script'),
  lora:                     ( val: 'Lora',                      path: 'Lora:400,700'),
  lato:                     ( val: 'Lato',                      path: 'Lato:100,300,400,700,900'),
