  overpassMono:             ( val: 'Overpass Mono',             path: 'Overpass+Mono:300,400,600,700'),
  playfairDisplay:          ( val: 'Playfair Display',          path: 'Playfair+Display:400,700,900'),
  PTSans:                   ( val: 'PT Sans',                   path: 'PT+Sans:400,700'),
  PTSansNarrow:             ( val: 'PT Sans Narrow',            path: 'PT+Sans+Narrow:400,700'),
  PTSansCaption:            ( val: 'PT Sans Caption',           path: 'PT+Sans+Caption:400,700'),
