  roboto:                   ( val: 'Roboto',                    path: 'Roboto:100,300,400,500,700,900'),
  robotoCondensed:          ( val: 'Roboto Condensed',          path: 'Roboto+Condensed:300,400,700'),
  robotoSlab:               ( val: 'Roboto Slab',               path: 'Roboto+Slab:100,300,400,700'),
  rubik:                    ( val: 'Rubik',                     path: 'Rubik:300,400,500,700,900'),
  rubikOne:                 ( val: 'Rubik One',                 path: 'Rubik+One:400'),
  rubikMonoOne:             ( val: 'Rubik Mono One',            path: 'Rubik+Mono+One:400'),
  sourceSansPro:            ( val: 'Source Sans Pro',           path: 'Source+Sans+Pro:200,300,400,600,700,900'),
);


@if $css-var == false {

  @each $font-name, $font in $gfont {
    @if '#{map-get($font, val)}' == '#{v(typography,main-font-family)}' {
      @import url(https://fonts.googleapis.com/css?family=#{map-get($font, path)});
    }
    @if '#{v(typography,main-font-family)}' != '#{v(typography,header-font-family)}' {
      @if '#{map-get($font, val)}' == '#{v(typography,header-font-family)}' {
        @import url(https://fonts.googleapis.com/css?family=#{map-get($font, path)});
      }
    }
  }

}@elseif{
  @each $font-name, $font in $gfont {
    // @debug map-get($font, val);
    @import url(https://fonts.googleapis.com/css?family=#{map-get($font, path)});
  }
}
